export const setXYZXYZ = (array, location, x, y, z) => {
    array[location + 0] = x;
    array[location + 1] = y;
    array[location + 2] = z;
    array[location + 3] = x;
    array[location + 4] = y;
    array[location + 5] = z;
};
export const setXY = (array, location, x, y) => {
    array[location + 0] = x;
    array[location + 1] = y;
};
export const setXYZ = (array, location, x, y, z) => {
    array[location + 0] = x;
    array[location + 1] = y;
    array[location + 2] = z;
};
export const setXYZW = (array, location, x, y, z, w) => {
    array[location + 0] = x;
    array[location + 1] = y;
    array[location + 2] = z;
    array[location + 3] = w;
};
